import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Drawer } from "@material-ui/core";

import UserDrawer from "./UserDrawer";

import Logo from "../../../images/lovelyarts.png";

import "../../../styles/navbar.css";

export default class Navbar extends Component {
  state = {
    // drawer
    rightDrawer: false,
  };

  // drawer user account
  toggleDrawer = (open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    this.setState({ rightDrawer: open });
  };

  closeDrawer = () => {
    this.setState({ rightDrawer: false });
  };

  render() {
    return (
      <React.Fragment>
        <nav className="navbar sticky-top p-3 client-nav-bar">
          <Link to="/">
            <img src={Logo} className="logo" alt="lovelyarts Logo" />
          </Link>
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <Link to="/login" className="nav-link">
                Login
              </Link>
              <Link to="/create-an-account" className="nav-link">
                Register
              </Link>
              <span className="nav-link" onClick={this.toggleDrawer(true)}>
                <i className="far fa-user user-icon"></i>
              </span>
            </li>
          </ul>
        </nav>

        <Drawer
          anchor={"right"}
          open={this.state.rightDrawer}
          onClose={this.toggleDrawer(false)}
        >
          <UserDrawer closeDrawer={this.closeDrawer} />
        </Drawer>
      </React.Fragment>
    );
  }
}
